/* ============= NAV ============= */
.nav {
    position: fixed;
    bottom: 2rem;
    left: 50%;
    transform: translateX(-50%);
    z-index: 100;
    display: flex;
    align-items: center;
    gap: 2rem;
    padding: 1rem 1.5rem;
    background: rgba(15, 20, 25, 0.85);
    border-radius: 3rem;
    border: 1px solid var(--color-primary-variant);
    backdrop-filter: blur(20px);
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
    animation: slideDown 0.6s ease-out both;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.3s ease, transform 0.3s ease;
}

.nav:hover {
    opacity: 1;
    pointer-events: auto;
}

body:hover .nav,
.nav:hover {
    opacity: 1;
    pointer-events: auto;
}

.nav--hidden {
    opacity: 0 !important;
    pointer-events: none !important;
}

.nav__trigger-zone {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100px;
    z-index: 99;
    pointer-events: none;
}

.nav__trigger-zone:hover ~ .nav,
.nav__trigger-zone:hover .nav {
    opacity: 1;
    pointer-events: auto;
}


.nav__links {
    display: flex;
    gap: 0.5rem;
    align-items: center;
}

.nav__link {
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background: transparent;
    color: var(--color-text-secondary);
    font-size: 1.3rem;
    cursor: pointer;
    transition: var(--transition);
    position: relative;
    border: 2px solid transparent;
}

.nav__link::before {
    content: '';
    position: absolute;
    inset: 0;
    background: radial-gradient(circle at center, var(--color-primary-variant), transparent);
    border-radius: 50%;
    opacity: 0;
    transition: opacity 0.3s ease;
    z-index: -1;
}

.nav__link:hover {
    color: var(--color-primary);
}

.nav__link:hover::before {
    opacity: 1;
}

.nav__link.active {
    background: var(--color-primary);
    color: var(--color-white);
    box-shadow: 0 0 20px rgba(139, 115, 85, 0.4);
}

.nav__link.active::before {
    opacity: 0;
}


.nav__controls {
    display: none;
    gap: 0.5rem;
    align-items: center;
    padding-left: 1rem;
    border-left: 1px solid var(--color-primary-variant);
}

.nav__control-btn {
    width: 45px;
    height: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid var(--color-secondary);
    background: transparent;
    color: var(--color-secondary);
    font-size: 1.2rem;
    border-radius: 50%;
    cursor: pointer;
    transition: var(--transition);
    font-weight: 600;
    position: relative;
    overflow: hidden;
}

.nav__control-btn::before {
    content: '';
    position: absolute;
    inset: 0;
    background: var(--color-secondary);
    border-radius: 50%;
    transform: scale(0);
    transition: transform 0.3s ease;
    z-index: -1;
}

.nav__control-btn:hover::before {
    transform: scale(1);
}

.nav__control-btn:hover {
    color: var(--color-white);
    border-color: var(--color-secondary);
}

.nav__theme-btn {
    border-color: var(--color-secondary);
    color: var(--color-secondary);
}

.nav__theme-btn::before {
    background: var(--color-secondary);
}

.nav__lang-btn {
    font-size: 0.85rem;
    letter-spacing: 1px;
}


@media screen and (max-width: 1024px) {
    .nav {
        padding: 0.8rem 1.2rem;
        gap: 1.5rem;
    }

    .nav__link,
    .nav__control-btn {
        width: 45px;
        height: 45px;
        font-size: 1.1rem;
    }

    .nav__lang-btn {
        font-size: 0.8rem;
    }
}

@media screen and (max-width: 600px) {
    .nav {
        bottom: 1.5rem;
        padding: 0.6rem 0.8rem;
        gap: 0.8rem;
        border-radius: 2.5rem;
    }

    .nav__links {
        gap: 0.3rem;
    }

    .nav__link {
        width: 40px;
        height: 40px;
        font-size: 1.1rem;
    }

    .nav__control-btn {
        width: 40px;
        height: 40px;
        font-size: 1rem;
    }

    .nav__lang-btn {
        font-size: 0.7rem;
        padding: 0;
    }

    .nav__controls {
        padding-left: 0.8rem;
        gap: 0.3rem;
    }
}


@keyframes slideUp {
    from {
        opacity: 0;
        transform: translateX(-50%) translateY(30px);
    }
    to {
        opacity: 1;
        transform: translateX(-50%) translateY(0);
    }
}

@keyframes slideDown {
    from {
        opacity: 0;
        transform: translateX(-50%) translateY(-30px);
    }
    to {
        opacity: 0;
        transform: translateX(-50%) translateY(0);
    }
}